import { FC, RefObject } from "react";
import { MainScreen } from "./main-screen/MainScreen.tsx";
import { HowItWorks } from "./how-it-works/HowItWorks.tsx";
//import { Photo } from "./photo/Photo.tsx";
import { ProductAndServices } from "./products-and-services/ProductsAndServices.tsx";
import { UseCasesOfOurProducts } from "./use-cases-of-our-products/UseCasesOfOurProducts.tsx";
import { Section } from "@/components/app/App.tsx";

//Types
type Props = {
  mainScreenRef: RefObject<HTMLElement>;
  sections: Section[];
};

//Component
export const MainPage: FC<Props> = ({ mainScreenRef, sections }) => {
  return (
    <>
      <MainScreen ref={mainScreenRef} />
      {sections.map((section) => {
        if (section.id === "1") {
          return (
            <HowItWorks
              key={section.id}
              id={section.id}
              title={section.title}
            />
          );
        }
        // if (section.id === "2") {
        //   return (
        //     <Photo key={section.id} id={section.id} title={section.title} />
        //   );
        // }
        if (section.id === "3") {
          return (
            <ProductAndServices
              key={section.id}
              id={section.id}
              title={section.title}
            />
          );
        }
        if (section.id === "4") {
          return (
            <UseCasesOfOurProducts
              key={section.id}
              id={section.id}
              title={section.title}
            />
          );
        }
        return null;
      })}
    </>
  );
};
