import styled from "styled-components";
import { divWithImage, sizeVariable } from "@/assets/styles/commonStyles.ts";
import { getSectionData } from "@/assets/constants/constants.ts";
import { useTranslation } from "react-i18next";

//Styles
export const StyledLogo = styled.img`
  ${divWithImage};
  width: fit-content;
  height: 2.63vw;

  @media (max-width: ${sizeVariable}) {
    width: fit-content;
    height: 5.06vw;
  }
`;

//Component
export const Logo = () => {
  const { t } = useTranslation();
  const logoData = getSectionData("Logo", t);

  if (logoData === null) {
    return null;
  }

  const { src, alt } = logoData;

  return src && alt && <StyledLogo src={src} alt={alt} />;
};
