import { forwardRef } from "react";
import styled from "styled-components";
import { Navigation } from "./navigation/Navigation.tsx";
import { SectionTitle } from "@/components/app/common/section-title/SectionTitle.tsx";
import { colorVariables, sizeVariable } from "@/assets/styles/commonStyles.ts";
import { Rates } from "./rates/Rates.tsx";
import { useTranslation } from "react-i18next";
import { getSectionData } from "@/assets/constants/constants.ts";

const { lightBlackColor } = colorVariables;

//Styles
const StyledPage = styled.main`
  width: 100vw;
`;

const StyledWrapper = styled.div`
  display: flex;
  flex-direction: column;
`;

const StyledContainer = styled.div`
  width: 100vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
`;

const StyledTitle = styled(SectionTitle)`
  margin: 3.33vw 5.4vw 4.51vw;
  text-align: start;

  @media (max-width: ${sizeVariable}) {
    margin: 3.33vw 6.4vw 4.51vw;
  }
`;

//Components
export const PaymentFeesPage = forwardRef<HTMLElement>((_, ref) => {
  const { t } = useTranslation();
  const pageData = getSectionData("PaymentFees", t);

  if (pageData === null) {
    return null;
  }

  const { title, individual, enterprise, navigation } = pageData;

  return (
    <StyledPage ref={ref}>
      {navigation && <Navigation navigation={navigation} />}
      <StyledWrapper>
        {title && <StyledTitle color={lightBlackColor}>{title}</StyledTitle>}
        <StyledContainer>
          {individual && <Rates rates={individual} />}
          {enterprise && <Rates rates={enterprise} />}
        </StyledContainer>
      </StyledWrapper>
    </StyledPage>
  );
});
