type Name = "Sections" | "Header" | "HowItWorks" | "PaymentFees" | "Logo";

type Section = {
  id: string;
  title: string;
};

type Sections = Section[];

type Logo = {
  src: string;
  alt: string;
};

// sectionData.ts

export const getSectionData = (
  name: Name,
  t: TFunction
): Sections | Logo | null => {
  switch (name) {
    case "Sections":
      return [
        { id: "1", title: t("sections.one") },
        // { id: "2", title: t("sections.two") },
        { id: "3", title: t("sections.three") },
        { id: "4", title: t("sections.four") },
      ];
    case "Header":
      return {
        loginAndRegister: {
          signUp: t("header.loginAndRegister.signUp"),
          signIn: t("header.loginAndRegister.signIn"),
        },
        language: {
          src: "images/header/language-icon-white.svg",
          alt: t("header.language.alt"),
          srcGray: "images/header/language-icon-gray.svg",
          text: t("header.language.text"),
        },
        menu: {
          src: "images/header/burger-icon.svg",
          alt: t("header.menu.alt"),
          text: t("header.menu.text"),
        },
      };
    case "HowItWorks":
      return {
        text: t("sectionHowItWorks.text"),
        stepOne: {
          image: {
            src: "images/main-page/how-it-works/send-invoice.svg",
            alt: t("sectionHowItWorks.stepOne.alt"),
          },
          subtitle: t("sectionHowItWorks.stepOne.subtitle"),
          text: t("sectionHowItWorks.stepOne.text"),
        },
        stepTwo: {
          image: {
            src: "images/main-page/how-it-works/payment.svg",
            alt: t("sectionHowItWorks.stepTwo.alt"),
          },
          subtitle: t("sectionHowItWorks.stepTwo.subtitle"),
          text: t("sectionHowItWorks.stepTwo.text"),
        },
        stepThree: {
          image: {
            src: "images/main-page/how-it-works/broadcast.svg",
            alt: t("sectionHowItWorks.stepThree.alt"),
          },
          subtitle: t("sectionHowItWorks.stepThree.subtitle"),
          text: t("sectionHowItWorks.stepThree.text"),
        },
        advantages: [
          {
            index: "1",
            subtitle: t("sectionHowItWorks.advantages.advantage1.subtitle"),
            text: t("sectionHowItWorks.advantages.advantage1.text"),
            image: "images/main-page/how-it-works/advantages/first-icon.svg",
            alt: t("sectionHowItWorks.advantages.advantage1.alt"),
          },
          {
            index: "2",
            subtitle: t("sectionHowItWorks.advantages.advantage2.subtitle"),
            text: t("sectionHowItWorks.advantages.advantage2.text"),
            image: "images/main-page/how-it-works/advantages/second-icon.svg",
            alt: t("sectionHowItWorks.advantages.advantage2.alt"),
          },
          {
            index: "3",
            subtitle: t("sectionHowItWorks.advantages.advantage3.subtitle"),
            text: t("sectionHowItWorks.advantages.advantage3.text"),
            image: "images/main-page/how-it-works/advantages/third-icon.svg",
            alt: t("sectionHowItWorks.advantages.advantage3.alt"),
          },
          {
            index: "4",
            subtitle: t("sectionHowItWorks.advantages.advantage4.subtitle"),
            text: t("sectionHowItWorks.advantages.advantage4.text"),
            image: "images/main-page/how-it-works/advantages/fourth-icon.svg",
            alt: t("sectionHowItWorks.advantages.advantage4.alt"),
          },
        ],
      };
    case "PaymentFees":
      return {
        title: t("pagePaymentFees.title"),
        individual: {
          subtitle: t("pagePaymentFees.subtitleIndividual"),
          linkHref: individualHref,
          linkLabel: t("pagePaymentFees.linkLabel"),
          depositFee: {
            text: t("pagePaymentFees.depositFee"),
            percent: ["0.1%", "0.3%"],
            span: ["<1000$", ">1000$"],
          },
          swapFee: {
            text: t("pagePaymentFees.swapFee"),
            percent: ["0.5%"],
          },
          withdrawFee: {
            text: t("pagePaymentFees.withdrawFee"),
            percent: ["0.5%"],
          },
          fiatBankIn: {
            text: t("pagePaymentFees.fiatBankIn"),
            percent: ["0.5%"],
          },
          fiatBankOut: {
            text: t("pagePaymentFees.fiatBankOut"),
            percent: ["0.5%"],
          },
          depositFeeSpan: {
            text: t("pagePaymentFees.depositFeeSpan"),
            percent: ["0.5%"],
          },
        },
        enterprise: {
          subtitle: t("pagePaymentFees.subtitleEnterprise"),
          linkHref: enterpriseHref,
          linkLabel: t("pagePaymentFees.linkLabel"),
          depositFee: {
            text: t("pagePaymentFees.depositFee"),
            percent: ["0.1%"],
          },
          swapFee: {
            text: t("pagePaymentFees.swapFee"),
            percent: ["0.5%", "0.6%"],
            span: ["<1000$", ">1000$"],
          },
          withdrawFee: {
            text: t("pagePaymentFees.withdrawFee"),
            percent: ["0.5%"],
          },
          fiatBankIn: {
            text: t("pagePaymentFees.fiatBankIn"),
            percent: ["0.5%"],
          },
          fiatBankOut: {
            text: t("pagePaymentFees.fiatBankOut"),
            percent: ["0.5%"],
          },
          depositFeeSpan: {
            text: t("pagePaymentFees.depositFeeSpan"),
            percent: ["0.5%"],
          },
        },
        navigation: {
          button: t("pagePaymentFees.navigation.button"),
          separator: ">",
          location: t("pagePaymentFees.navigation.location"),
        },
      };
    case "Logo":
      return {
        src: "images/common/logo.svg",
        alt: t("logo.alt"),
      };
    default:
      return null;
  }
};
